h1, h2, h3, h4, h5, h6 {
  font-family: 'Open Sans Condensed', sans-serif;
}

.container-fluid {
  max-width: 1800px;
  background: #fff;
}

.pagination li a {
  background: #eaf5fc;
  border: none;
  padding: 12px 19px;
}

.pagination li a:hover {
  background: #54a561;
  color: #fff;
}

.pagination li:first-child a, .pagination li:last-child a {
  border-radius: 0;
}

.pagination .active a {
  background: #54a561;
  border: none;
}

.pagination .active a:hover {
  background: #54a561;
  border: none;
}

body {
  background: #f6f6f6;
}

.header {
  z-index: 2;
  position: relative;
  transition: .2s;
}

.header__pinned {
  background: #fff;
}

body.home .header__pinned {
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.2);
}

.super {
  background: #fff;
  margin-left: -15px;
  margin-right: -15px;
  padding: 15px;
}

.super .flex {
  display: flex;
  align-items: center;
}

.super__logo {
  width: 100%;
  max-width: 350px;
  position: relative;
  display: block;
  margin: 0 auto;
}

.super__phones {
  justify-content: flex-end;
  display: flex;
  flex-wrap: wrap;
  font-size: 18px;
  font-family: "Lato", "Helvetica Neue", "Helvetica", Helvetica, Arial, sans-serif;
}

.super__phones__wrapper {
  margin-left: 15px;
}

.super__phones span {
  color: #737373;
  font-size: 14px;
}

.header__wrap {
  background: #c7e4f3;
  margin-left: -15px;
  margin-right: -15px;
  padding: 0 15px;
}

.heeader-nav__ul {
  margin: 0;
  padding: 0;
  text-align: center;
}

.heeader-nav__li {
  list-style: none;
  display: inline-block;
  position: relative;
}

.heeader-nav__link {
  display: inline-block;
  position: relative;
  padding: 20px 30px;
  color: #000;
  transition: .2s;
}

.heeader-nav__link:hover {
  text-decoration: none;
  color: #54a561;
}

.square__center {
  background: #e5f5ff;
  height: 100vh;
  max-height: 1000px;
  min-height: 800px;
  position: relative;
  overflow: hidden;
}

.square__center:after, .square__center:before {
  left: 0;
  content: '';
  display: block;
  position: absolute;
  width: 100%;
  background-repeat: repeat-x;
  background-position: center bottom;
}

.square__center:before {
  bottom: -1px;
  background-image: url("../img/gress.png");
  height: 200px;
}

.square__logo {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  top: 20%;
}

.square__info {
  position: absolute;
  left: 20%;
  top: 70%;
}

.square__not-center {
  height: 50vh;
  max-height: 500px;
  background: #fff;
  min-height: 400px;
  position: relative;
  display: block;
  overflow: hidden;
}

.square__not-center.active .square__image {
  transform: scale(1.1);
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.2);
}

.square__image {
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center bottom;
  position: absolute;
  top: 40%;
  margin-top: -100px;
  left: 50%;
  margin-left: -100px;
  height: 200px;
  width: 200px;
  border-radius: 50%;
  transition: 1.5s;
}

.square__title {
  display: block;
  position: absolute;
  bottom: 0;
  height: 70px;
  left: 0;
  right: 0;
  text-align: center;
}

.square__1 {
  background: #f3f8ff;
}

.square__2 {
  background: #d2e6f4;
}

.square__3 {
  background: #d7f3ff;
}

.square__4 {
  background: #f0ffff;
}

.square__link {
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
}

#typed {
  font-size: 30px;
  font-family: 'Open Sans Condensed', sans-serif;
}

.typed-cursor {
  color: #000;
  font-size: 30px;
  font-weight: 700;
  font-family: 'Open Sans Condensed', sans-serif;
}

.accent {
  color: #54a561;
}

#cloud {
  left: 0;
  display: block;
  position: absolute;
  width: 100%;
  background-repeat: repeat-x;
  background-position: center bottom;
  top: 0;
  height: 250px;
  background-image: url("../img/cloud.png");
  background-size: cover;
  transition: .05s;
}

.info {
  overflow: hidden;
  position: relative;
}

.info__wrapper {
  padding: 30px 15px;
}

.info__news {
  background: #90c8f1;
  min-height: 300px;
  position: relative;
}

.info__objects {
  background: #7ebfee;
  min-height: 300px;
}

.info__callbac {
  background: #6cb6ec;
  min-height: 300px;
}

.info__title {
  margin-top: 0;
}

.info__title__absolute {
  position: absolute;
  display: block;
  top: 0;
  left: 0;
  padding: 5px 15px;
}

.info .owl-next,
.info .owl-prev {
  display: block;
  position: absolute;
  top: -33px;
  color: #ebebeb;
}

.info .owl-next {
  right: 10px;
}

.info .owl-prev {
  right: 50px;
}

.last-news__content {
  margin-bottom: 20px;
}

.last-news__image {
  background-size: cover;
  background-position: center;
  position: absolute;
  top: 0;
  left: -1px;
  height: 300px;
  width: 100%;
}

.last-news__image:after {
  content: '';
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(157, 207, 234, 0.5);
}

.last-news__title-absolute {
  position: absolute;
  top: 60px;
  z-index: 2;
  left: 15px;
}

.last-news__read-absolute {
  position: absolute;
  z-index: 2;
  top: 250px;
  right: 0;
  padding: 5px 15px;
  background: #7ebfee;
  color: #fff;
  text-decoration: none;
}

.last-news__read-absolute:hover {
  background: #54a561;
  color: #fff;
  text-decoration: none;
}

.info__objects {
  height: 300px;
  padding: 0;
}

.info__objects .owl-carousel .owl-stage-outer,
.info__objects .owl-carousel {
  height: 300px;
}

body.home .super {
  display: none;
}

body.home .header__wrap {
  background: none;
  position: fixed;
  width: 100%;
  left: 0;
  right: 0;
  top: 0;
  margin: 0;
}

body.home .header__pinned {
  background: #fff;
  z-index: 2000;
}

.breadcrumbs .breadcrumb {
  margin-bottom: 0;
  background: #dceef8;
}

.breadcrumbs .breadcrumb a:hover {
  color: #54a561;
}

.about-us .row {
  background: #edf6fb;
}

.about-us__text {
  padding: 20px 0;
}

.geo__map {
  background: #fefeff;
  padding: 20px 150px;
}

.geo__map .is_objects {
  fill: #9dcfea;
  transition: .2s;
}

.geo__map .is_objects:hover {
  fill: #54a561;
  cursor: pointer;
}

.geo__products .without-padding {
  padding-left: 0;
  padding-right: 0;
}

.object-detail__image {
  background-position: center;
  background-size: cover;
  width: 100%;
  height: 600px;
  position: relative;
  margin: 0 auto;
}

.pre-footer__bg {
  background: #dceef8;
  padding: 30px 0;
}

.pre-footer__nav {
  margin: 0;
  padding: 0;
}

.pre-footer__nav__li {
  list-style: none;
}

.footer__bg {
  background: #c7e4f3;
  padding: 15px 0;
}

.page-title__title {
  background: #edf6fb;
  margin-bottom: 0;
  padding: 20px 0;
  margin-top: 0;
}

.page-title__secondary-title {
  background: #edf6fb;
  margin-bottom: 0;
  padding: 20px 0;
  margin-top: 0;
}

.filter {
  display: flex;
  justify-content: center;
  background: #fff;
}

.filter__link {
  width: 100%;
  text-align: center;
  padding: 15px;
  text-decoration: none;
  color: #000;
  display: block;
  background: rgba(157, 207, 234, 0.3);
}

.filter__link:hover {
  text-decoration: none;
  color: #fff;
  background: #54a561;
}

.products__grid {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.prod_hover.active img {
  transform: scale(1.1);
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.2);
}

.product {
  width: 100%;
  min-width: 250px;
  max-width: 300px;
  position: relative;
  transition: .2s;
}

.product:nth-child(5n) {
  background: #eaf4f9;
}

.product:nth-child(5n-1) {
  background: #e0eef7;
}

.product:nth-child(5n-2) {
  background: #e9f0fb;
}

.product:nth-child(5n-3) {
  background: #d2e6f4;
}

.product:nth-child(5n-4) {
  background: #deeff7;
}

.product img {
  transition: ease-in-out 1s;
  border-radius: 50%;
}

.product:hover {
  background: rgba(162, 209, 243, 0.3);
}

.product__body {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 300px;
}

.product__image-wrapper {
  transition: .2s;
}

.product__footer {
  text-align: center;
  padding-bottom: 10px;
}

.product__link {
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
}

.product__edit {
  position: fixed;
  bottom: 30px;
  right: 30px;
  width: 70px;
  height: 70px;
  background: #a9294a;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
  color: #fff;
  transition: .2s;
}

.product__edit:hover, .product__edit:focus, .product__edit:active {
  color: #fff;
  background: #54a561;
  text-decoration: none;
}

.product-detail .container-fluid {
  background: #fff;
}

.product-detail__image {
  width: 300px;
  height: 300px;
  border-radius: 50%;
  border: 5px solid #fff;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.08);
}

.product-detail__image__main {
  display: inline-block;
  position: relative;
}

.product-detail__image__main:after {
  content: '';
  display: block;
  position: absolute;
  top: 50%;
  left: 50%;
  width: 100px;
  height: 100px;
  background: url("data:image/svg+xml;utf8;base64,") no-repeat center;
  margin-left: -50px;
  margin-top: -50px;
  transition: .4s ease-in-out;
  transform: scale(0);
}

.product-detail__image__main:before {
  content: '';
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: transparent;
  border-radius: 50%;
  transition: .4s ease-in-out;
}

.product-detail__image__main:hover:before {
  background: rgba(0, 0, 0, 0.2);
}

.product-detail__image__main:hover:after {
  transform: scale(1);
}

.product-detail__image__wrapper {
  text-align: center;
  padding: 30px 0;
  background: #fff;
}

.product-detail__desc__section {
  padding: 30px 0;
}

.product-detail__characteristics {
  background: #f8fbfe;
  padding: 30px 0;
}

.product-detail__benefits {
  padding: 30px 0;
  background: #eff7fd;
}

.product-detail__benefit {
  text-align: center;
}

.product-detail__gallery {
  padding: 30px 0;
  background: #e6f3fc;
}

.product-detail__images {
  transition: all ease-in-out .2s;
  background: #fff;
  padding: 5px;
  margin: 5px;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.05);
}

.product-detail__images__wrapper {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}

.product-detail__images:hover {
  transform: scale(1.1);
}

.product-detail__images--shadow {
  position: relative;
  filter: grayscale(1);
}

.product-detail__form__section {
  background: #a2d1f3;
  padding: 30px 0;
}

.product-detail__form__button {
  background: #54a561;
  border: none;
  padding: 20px 50px;
  color: #fff;
  font-size: 1.4em;
  width: 100%;
  transition: .2s;
  font-family: "Open Sans Condensed", serif;
  font-weight: 500;
}

.product-detail__form__button:hover {
  background: #43834d;
}

.product-detail__form__input {
  font-family: "Open Sans Condensed", serif;
  font-weight: 500;
  font-size: 1.4em;
  padding: 30px 10px;
  border: none;
}

.product-detail__docs__section {
  padding: 30px 0;
  background: #ddeefb;
}

.product-detail__docs .file-icon {
  width: 50px;
  height: auto;
  margin-right: 20px;
}

.product-detail__video {
  width: calc(50% - 20px);
  background: #a2d1f3;
  padding: 10px;
  margin: 5px;
}

.product-detail__video__title {
  margin-bottom: 0;
}

.product-detail__video__section {
  padding: 30px 0;
  background: #d4eaf9;
}

.product-detail__video__wrapper {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}

.product-detail__video__wrapper .plyr__play-large {
  padding: 24px;
}

.product-detail__digits {
  padding: 30px 0;
  background: #eff7fd;
}

.product-detail__digit {
  text-align: center;
}

.product-detail__digit__main {
  font-size: 70px;
  font-weight: 100;
  font-family: "Open Sans Condensed", serif;
}

.single-news__image {
  float: left;
  margin-right: 15px;
  margin-bottom: 15px;
  clear: both;
}

.single-news__text {
  padding: 30px 15px;
  background: #fff;
}

.news-list__wrapper {
  background: #fff;
  display: flex;
  flex-wrap: wrap;
}

.news-item {
  position: relative;
  width: 25%;
  padding: 15px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.news-item__link {
  text-decoration: none;
}

.news-item__link:hover {
  text-decoration: none;
  color: #54a561;
}

.news-item__body {
  text-align: justify;
}

.news-item__footer {
  margin-top: 15px;
  text-align: right;
  color: #737373;
}

.form-control,
.alert,
.btn {
  border-radius: 0;
}

.btn-success {
  background: #54a561;
}

.sign-in__wrapper {
  padding: 30px 0;
}

.sign-in .container-fluid {
  background: #fff;
}

.sign-in .btn-success {
  padding: 10px 30px;
}

.sign-in__center {
  text-align: center;
}

.profile-header {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
}

.profile-header__avatar {
  border: 3px solid #dceef8;
  border-radius: 50%;
  display: block;
  position: relative;
  background-size: cover;
  background-position: center;
  width: 70px;
  height: 70px;
  margin-right: 15px;
}

.user__avatar {
  display: block;
  width: 150px;
  height: 150px;
  border-radius: 50%;
  background-size: cover;
  background-position: center;
  position: relative;
  left: 50%;
  margin-left: -75px;
  border: 3px solid #dceef8;
}

.contacts__map {
  width: 100%;
  height: 500px;
}

@media (max-width: 1200px) {
  .square__logo {
    width: 80%;
    max-width: 501px;
  }
  .square__title {
    font-size: 24px;
  }
}

@media (max-width: 991px) {
  .heeader-nav__link {
    padding: 20px;
  }
  .square__center {
    background: #e5f5ff;
    height: auto;
    max-height: none;
    min-height: 600px;
  }
  .square__logo {
    width: 80%;
    max-width: 501px;
  }
  .square__title {
    font-size: 24px;
  }
}
